export default {
	data() {
		return {
			listData: [{
				'buildingsId': '1',
				'name': '1幢',
				'units': [{
					'uName': '1单元',
					'unitsId': '1',
					'status': '在线',
					"type": "1"
				}]
			}],

			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			isDisable: false,
			inputResult: '搜索幢数如:6幢'
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
	},
	created() {
		this.getData();
	},

	onReady() {
		uni.hideLoading()
	},

	methods: {
		getData() {
			var self = this;
			const requestTask = uni.request({
				url: 'http://localhost:3000/moniData',
				success(res) {
					self.listData = res.data.buildings;
					console.log(self.listData);
				}
			})
		},
		searchBuilding() { //跳转搜索结果界面
		   var result = this.inputResult.substring(this.inputResult.indexOf(":")+1,this.inputResult.length);
			uni.navigateTo({
				url: `../home/search_result?inputResult=${result}`
			})
		},
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			if (this.load) {
				for (let i = 0; i < this.listData.length; i++) {
					let view = uni.createSelectorQuery().select("#main-" + this.listData[i].id);
					view.fields({
						size: true
					}, data => {
						console.log("打印错误：" + data)
						/*data.height 有可能为  */
						if (null != data) {
							tabHeight = tabHeight + data.height;
						}
						this.listData[i].top = tabHeight;
						this.listData[i].bottom = tabHeight;
					}).exec();
				}
				this.load = false;
			}
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.listData.length; i++) {
				if (scrollTop > this.listData[i].top && scrollTop < this.listData[i].bottom) {
					this.verticalNavTop = (this.listData[i].id - 1) * 50;
					this.tabCur = this.listData[i].id;
					console.log(scrollTop);
					return false;
				}
			}
		},
		toDeviceDetail(val, val2) { //跳转设备详情
			uni.navigateTo({
				url: `../device/device_detail?buidldingId=${val}&unitId=${val2}`
			})
		},
		openLock(val) {
			var isSuccess = false;
			console.log(val.uName + ":" + val.type + ":" + val.status);
			if (val.type !== "1") {
				uni.showToast({
					image: '../../static/img/ic_offline.png',
					title: "设备已离线"
				})
				return;
			}
			if (isSuccess) {
				uni.showToast({
					image: '../../static/img/ic_success.png',
					title: '开锁成功'
				})
			} else {
				uni.showModal({
					title: '提示',
					cancelColor: '#A8A8A9',
					content: '开锁失败，请重试!',
					confirmColor: '#4C7DFD',
					success(res) {
						if (res.confirm) {
							console.log("点击确认");
						} else {
							console.log("点击取消");
						}
					}
				})
			}
		}
	}
}
